import { Injectable } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';

import { throwError, Observable } from 'rxjs';
import { environment } from '../../environments/environment';
import { tap } from 'rxjs/operators';
import { ActivatedRoute } from '@angular/router';
//import { JwtService } from '../services/jwt.service';

@Injectable({
  providedIn: 'root'
})
export class JwtInterceptor implements HttpInterceptor {

  static SESSION_STORAGE_TOKEN: string = 'auth-token';

  constructor(
    private route: ActivatedRoute
    //,private jwtService: JwtService
  ) {
    //console.log("Usuario: " + jwtService.getUsername());
  }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    request = request.clone({
      setHeaders: {
        //Authorization: `Bearer ${this.jwtService.getToken()}`
        Authorization: `Bearer ${sessionStorage.getItem(JwtInterceptor.SESSION_STORAGE_TOKEN)}`
      }
    });
    return next.handle(request).
      pipe(
        tap((event: HttpEvent<any>) => {
          if (event instanceof HttpResponse) {
            this.log('HttpResponse', `${environment.projectName}: request realizado=${event.status}/${event.statusText}`);
          }
        }, error => {
          console.log(`Could not handle request: ${JSON.stringify(error)}`);
          this.logError('HttpResponse', error)
        })
      )
  }


  private log<T>(operation: string, result: T) {
    console.log(`Carga de configuraÃ§Ãµes realizada: ${operation} `);

    return result;
  }

  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }

    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return throwError(
      'Something bad happened; please try again later.');
  }
}
