import { Pipe, PipeTransform } from '@angular/core';
import { LovVals } from '../models/lovvals.model';

@Pipe({
  name: 'fetchDropDownLabel',
  pure: false
})
export class DropDownLabelPipe implements PipeTransform {
    transform(allValues: LovVals[], value: string):string {
        let itemValue = allValues.find(item => item.codigo===value);
        return (itemValue===undefined)?"":itemValue.descricao;
    }
}

