import { I18n, CustomDatepicker } from '../util/CustomDatepicker';
import { BrDateFormatter } from '../util/DateFormatters';
import { LovVals } from '../models/lovvals.model';

@Component({
  selector: 'politica-edit',
  templateUrl: './politica-edit.component.html',
  styleUrls: ['../politica.component.scss'],
  providers: [
    I18n, { provide: NgbDatepickerI18n, useClass: CustomDatepicker },
    { provide: NgbDateParserFormatter, useClass: BrDateFormatter }]
})

export class PoliticaEditComponent implements OnInit {
  subtitle: string;
  resourceId: string;
  openType: string;
  public bureausArray = [];
  contador: number = 0;
  isChecked = [] as boolean[];
  public filaData = [];
  manualdata: any;

  goBackLink: string;

  errors: string;

  modelDataIni: NgbDateStruct;
  modelDataFim: NgbDateStruct;

  bureaus$: Observable<PoliticaBureau[]>;
  filass$: Observable<Fila[]>;
  lovDSACTBUR: LovVals[] = [{ id: "DSACTBUR", codigo:"0", descricao:'NEGATIVAR', dadoPadrao:""}, { id: "DSACTBUR", codigo:"1", descricao:'REABILITAR', dadoPadrao:""}];
  lovTPClIDEV: LovVals[] = [{ id: "TPClIDEV", codigo:"0", descricao:'TITULAR', dadoPadrao:""},{ id: "TPClIDEV", codigo:"1", descricao:'AVALISTA', dadoPadrao:""}];

  selectedBureau: any = {};

  content: Politica = new Politica();
  originalCopy: Politica;

  faCalendarAlt = faCalendarAlt;
  faCaretDown=faCaretDown;
  faCaretUp=faCaretUp;

  static LOCALE_KEY = 'currentLocale';

  private politicaSubs: Subscription;
  private bureauSubs: Subscription;

  /*contentForm = new FormGroup({
    "cdPolitica": new FormControl('', [
      Validators.required,
      Validators.maxLength(1)
    ]),
    "lstBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(4)
    ]),
    "dsAcaoBureau": new FormControl('', [
      Validators.required,
      Validators.maxLength(2)
    ]),
    "tpCliDevedor": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dtIniVig": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ]),
    "dtFimVig": new FormControl('', [
      Validators.required,
      Validators.maxLength(10)
    ])
  });*/

  constructor(private route: ActivatedRoute,
    private router: Router,
    private politicaService: PoliticaService,
    private filaService: FilaService,
    private toastr: ToastrService,
    public translate: TranslateService) {
    this.bureaus$ = politicaService.bureaus$;
    this.filass$ = filaService.filas$;
    /*bureauService.bureaus$.subscribe( data => {
      data.forEach(objItem => this.selectedBureau[objItem.descBureau] = (this.selectedBureau[objItem.descBureau]!=undefined)?this.selectedBureau[objItem.descBureau]:false);
    });*/
    translate.setDefaultLang('ptBR');
    const currentLocale = sessionStorage.getItem(PoliticaEditComponent.LOCALE_KEY);

    translate.setDefaultLang(currentLocale);
  }

  ngOnInit() {

    this.filaService.load();
    this.politicaService.loadBureaus();

    this.goBackLink = '/external-' + this.politicaService.getResourceName();
    this.resourceId = this.route.snapshot.paramMap.get('code');
    this.openType = this.route.snapshot.paramMap.get('openType');
    this.subtitle = 'POLITICA.ITEMSUBTITLE.' + this.openType.toUpperCase();

    if (this.resourceId === null || this.resourceId == undefined) {
      this.politicaSubs = this.politicaService.startNewItem()
        .subscribe(politica => {
          this.content = politica;
        });
    } else
      this.politicaSubs = this.politicaService.selectItem(this.resourceId)
        .subscribe(politica => {
          this.content = (politica === null) ? this.content : politica
          this.content.lstBureau.forEach(data => {
            this.selectedBureau[data] = true;
          });
        });
    
    if(this.content.lstPoliticaBureau === null || this.content.lstPoliticaBureau == undefined){
      this.content.lstPoliticaBureau = [];
    }
    
    this.bureauSubs =  this.bureaus$.subscribe((data) => {
      this.bureausArray = Array.from(Object.keys(data), k=>data[k]);
      let bureaus = this.bureausArray
      let lista = this.content.lstBureau
      if(this.content.lstBureau == undefined){
        this.content.lstBureau = [];
      }

      bureaus.forEach(i => {
        lista.forEach(c => {
          if(i.descBureau === c){
            this.isChecked.push(true);
          }
        });
        if(this.isChecked[this.contador] == undefined){
          this.isChecked.push(false);
        }
        console.log(this.isChecked[this.contador])
        this.contador = this.contador + 1;
      });   
    }); 

    //this.originalCopy = JSON.parse(JSON.stringify(this.content));
    let arrIni = this.content.dtIniVig.split("/");
    this.modelDataIni = NgbDate.from({ year: Number(arrIni[2]), month: Number(arrIni[1]), day: Number(arrIni[0]) });
    let arrFim = this.content.dtFimVig.split("/");

    this.modelDataFim = NgbDate.from({ year: Number(arrFim[2]), month: Number(arrFim[1]), day: Number(arrFim[0]) });
    console.log(this.content.cdPolitica);
    //this.contentForm.patchValue(this.content);

    if (this.content.lstPoliticaBureau === null || this.content.lstPoliticaBureau === undefined) {
      this.content.lstPoliticaBureau = [];
    }

    //console.log(this.content.dtIniVig.substr(0, 3).concat(this.content.dtIniVig.substr(4, 6)))
    //console.log(`Validador : ${this.content.dtIniVig.includes("0", 4)}`);
    //console.log(this.content.dtIniVig.length)
  }

  ngOnDestroy() {
    console.log('Cleaning Politica...');
    if (this.politicaSubs != undefined) this.politicaSubs.unsubscribe();
    if (this.bureauSubs != undefined) this.bureauSubs.unsubscribe();
  }

  toggle(item: string) {
    this.selectedBureau[item] = !this.selectedBureau[item];
  }
/*
  onKeyIni(event: any) {
    if (event.target.value)
    this.content.dtIniVig = event.target.value;
    console.log(this.content.dtIniVig);
  }

  onKeyFim(event: any) {
    this.content.dtFimVig = event.target.value;
    console.log(this.content.dtFimVig);
  }
*/
  /*
    console.log("Antes:"+this.content.dtIniVig);
    if(!selected.day || !selected.month || !selected.year){
      this.content.dtIniVig = this.content.dtIniVig;
    }
    else{
    
    }
    console.log("Depois:"+this.content.dtIniVig);
  */

  onDataIniChange(selected: NgbDate) {
    this.content.dtIniVig = `${selected.day}/${selected.month}/${selected.year}`;
  }


  onDataFimChange(selected: NgbDate) {
    this.content.dtFimVig = `${selected.day}/${selected.month}/${selected.year}`;
  }

  setRemoverTemplate() {

  }
  save() {
    switch (this.openType) {
      case 'remove':
        this.politicaService.remove(this.content.cdPolitica, () => this.exit());
        break;
      case 'change':
        //this.content.dtIniVig = `${this.content.dtIniVig.substr(8,2)}/${this.content.dtIniVig.substr(5,2)}/${this.content.dtIniVig.substr(0,4)}`;
        //this.content.dtFimVig = `${this.content.dtFimVig.substr(8,2)}/${this.content.dtFimVig.substr(5,2)}/${this.content.dtFimVig.substr(0,4)}`;
        this.politicaService.save([this.content], () => this.exit());
        break;
      case 'create':
        if (this.politicaService.validItem(this.content.cdPolitica.toString()) === 0) {
          //this.content.dtIniVig = `${this.content.dtIniVig.substr(8,2)}/${this.content.dtIniVig.substr(5,2)}/${this.content.dtIniVig.substr(0,4)}`;
          //this.content.dtFimVig = `${this.content.dtFimVig.substr(8,2)}/${this.content.dtFimVig.substr(5,2)}/${this.content.dtFimVig.substr(0,4)}`;
          this.politicaService.include(this.content, () => this.exit());
        }
        else {
          let msg = `JÃ¡ existe uma politica cadastrada com este cÃ³digo`;
          this.toastr.error(msg, "Erro:");
        }
        break;

      default:
        break;
    }
  }

  onChange(cdBureau: number, descBureau: string, event) {
    const checked = event.target.checked;

    if (checked) {
      this.content.lstBureau.push(descBureau);
      this.content.lstPoliticaBureau.push(new PoliticaBureau(cdBureau, descBureau, this.content.cdPolitica, '', ''))
      console.log(this.content.lstPoliticaBureau);
    } else {
      const index = this.content.lstBureau.indexOf(descBureau);
      this.content.lstBureau.splice(index, 1);
      const index2 = this.content.lstPoliticaBureau.findIndex(item => (item['cdBureau'] === cdBureau));
      this.content.lstPoliticaBureau.splice(index2, 1);
      console.log(this.content.lstPoliticaBureau);
    }

  }

  cancel() {
    this.content = this.originalCopy;
    //let contentId = this.content ? this.content.codigoCriterioRemuneracao : null;
    this.router.navigate([this.goBackLink]);
  }

  exit(): void {
    this.router.navigate([this.goBackLink]);
  }

  get diagnostic() { return JSON.stringify(this.content); }

}
