import { Injectable, PipeTransform } from '@angular/core';
import { HttpHeaders, HttpClient, HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable, of, Subject, throwError } from 'rxjs';
import { tap, catchError, debounceTime, switchMap,delay, finalize } from 'rxjs/operators';
import { State } from '../models/state.model';
import { SearchResult } from '../models/search-result.model';
import { SortDirection } from '../directives/sortable.directive';
import { Politica } from '../models/politica.model';
import { DecimalPipe } from '@angular/common';
import { HandleError, HttpErrorHandler } from './http-error-handler.service';
import { ToastrService } from 'ngx-toastr';
import { environment } from '../../environments/environment';
import { PoliticaBureau } from '../models/bureau.model';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(items: Politica[], column: string, direction: string): Politica[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(politica: Politica,cdPolitica:string, text: string,pipe: PipeTransform) {
  const term = text.toLowerCase();
  return (politica.cdPolitica.toLowerCase().includes(term)
    || politica.dtIniVig.toLowerCase().includes(term)
    || politica.dtFimVig.toLowerCase().includes(term)
    || politica.dsAcaoBureau.toLowerCase().includes(term)
    || politica.lstBureau.toString().toLowerCase().includes(term));
}

function fullMatch(politica: Politica, cdPolitica: string) {
  return (politica.cdPolitica === cdPolitica);
}

function validItemB(politica: Politica, cdPolitica:string){
  return (politica.cdPolitica.toString() === cdPolitica);
}

@Injectable({
  providedIn: 'root'
})
export class PoliticaService {

  //private serviceUrl = 'politica';
  private _bureaus$ = new BehaviorSubject<PoliticaBureau[]>([]); 
  private _items$ = new BehaviorSubject<Politica[]>([]);
  private _serviceName = "Politica"; 
  private listaItems: Politica[] = [];
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _search$ = new Subject<void>();
  private _total$ = new BehaviorSubject<number>(0);
  private handleError: HandleError;

  private originalCopy: Politica = null;
  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
              private pipe: DecimalPipe,
              private httpErrorHandler: HttpErrorHandler,
              private toastr: ToastrService) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._items$.next(result.items);
      this._total$.next(result.total);
      
    });
    
  }

  get items$(){return this._items$.asObservable();}
  get bureaus$(){return this._bureaus$.asObservable();}
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }
  set codigo(codigo: string) { this._set({ codigo }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  getResourceName():string{
    return 'politica';
  }

  startNewItem():Observable<Politica>{
    let selectedItem:Politica = new Politica();
    this.originalCopy = null;
    //this._selectedItem$.next(this.selectedItem);
    return of(selectedItem);
  }
  
  selectItem(cdPolitica: string):Observable<Politica>{
    if (cdPolitica === null  || cdPolitica === undefined) {
      console.log(`${this._serviceName}: PolÃ­tica selecionada Ã© invÃ¡lida!`);
      let msg = `Bureau escolhido Ã© invÃ¡lido!`;
      this.toastr.error(msg, "CyberMessenger");
      return of(null);
    }
    let selectedItem = this.listaItems.find(item => fullMatch(item, cdPolitica));
    this.originalCopy =JSON.parse(JSON.stringify(selectedItem));
    return of(selectedItem); 
  }

  rollbackChanges() {
    if (this.originalCopy === null) return;
    let selectedIndex = this.listaItems.findIndex(item => fullMatch(item, this.originalCopy.cdPolitica.toString()));
    this.listaItems[selectedIndex] = this.originalCopy;
    this._items$.next(this.listaItems);
  }
  
  /* ValidaÃ§Ãµes */

  validItem(cdPolitica: string){
       let items = this.listaItems.filter(item => validItemB(item, cdPolitica));
       return items.length;
  }

  private _search(): Observable<SearchResult<Politica>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm,codigo } = this._state;

    // 1. sort
    let items = sort(this.listaItems, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, codigo, searchTerm,this.pipe));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }
  
  load(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de politicas selecionadas");
    this.getPoliticas().subscribe(
        data => {
          this.listaItems = data;
          this._items$.next(this.listaItems);
          this._search$.next();
        }
      );
  }

  getPoliticas(): Observable<Politica[]> {
    this._loading$.next(true);
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<Politica[]>(`${environment.serviceUrls.politicaUrl}/listarPoliticaNova/`)
    .pipe(
      finalize(() => this._loading$.next(false)),
      catchError(this.handleError<Politica[]>('Carregar itens', []))
    );
  }

  loadBureaus(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Buscando lista de Bureaus");
    this.getBureaus().subscribe(
      data => {
        this._bureaus$.next(data);
      }
    );
  }

  getBureaus(): Observable<PoliticaBureau[]> {
    //this._loading$.next(true);
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de items selecionados");
    return this.http.get<PoliticaBureau[]>(`${environment.serviceUrls.politicaUrl}/listaBureau/`)
      .pipe(tap(_ => console.log(`fetched items`)),
        catchError(this.handleError('getPoliticaBureau', []))
      );
  }

  
  remove(cdPolitica: string, done): void {
    this._loading$.next(true);
    let retVal: Observable<Politica> = this.http.delete<Politica>(`${environment.serviceUrls.politicaUrl}/${cdPolitica}`, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(changed => { if (changed) console.log(`${this._serviceName}: Registro removido com sucesso!`); }),
      catchError(this.handleError<Politica>('Remover'))
    ).subscribe(response => {
      if (response) {
        let itemIndex = this.listaItems.findIndex((item) => item['cdPolitica'] === cdPolitica);
        if (itemIndex >= 0) {
          this.listaItems.splice(itemIndex,1);
          console.log(`Item [${cdPolitica}] removido com sucesso`);
        }
        let msg = `Item removido com sucesso!`;
        this.toastr.success(msg, "CyberMessenger");
        this._items$.next(this.listaItems);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar polÃ­tica ${cdPolitica}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  include(item: Politica, done): void {
    //return this.http.put(`${environment.serviceUrls.empresaBureauUrl}/${item[keyName]}`, item, httpOptions).pipe(
    this._loading$.next(true);
    let retVal: Observable<Politica> = this.http.put<Politica>(`${environment.serviceUrls.politicaUrl}`, item, httpOptions);

    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap(data => { if (data['entity'] === null) console.log(`${this._serviceName}: Ocorreu um erro durante a incluisÃ£o do item cÃ³digo=${item['cdPolitica']}.`); }),
      catchError(this.handleError<Politica>('Incluir'))
    ).subscribe(response => {
      if (response) {
          console.log(`${this._serviceName}: Item cÃ³digo=${response['entity']['cdPolitica']} incluido com sucesso!`);
          let msg =  `Politica ${response['entity']['cdPolitica']} salvo com sucesso!`; 
          this.toastr.success(msg, "CyberMessenger");
          
          //this.listaItems.push(item);
          this.listaItems.unshift(response['entity']);
          this._items$.next(this.listaItems);
        done();
      }
    }, error => {
      let msg = `Ocorreu uma falha ao atualizar polÃ­tica ${item.cdPolitica}. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao remover registro: ' + error.error.message);
    });
  }

  save(items: Politica[], done): void {
    this._loading$.next(true);
    
    let retVal: Observable<Politica[]> = this.http.post<Politica[]>(environment.serviceUrls.politicaUrl, items, httpOptions);
    
    retVal.pipe(
      finalize(() => this._loading$.next(false)),
      tap((changedList: any) => {if(changedList['entity']==null){console.log(`${this._serviceName}: Ocorreu um erro durante a atualizaÃ§Ã£o do item.`);}}),
      catchError(this.handleError<Politica[]>('Salvar'))
    ).subscribe(response => {
      if (response) {
        console.log(`${this._serviceName}: Foram atualizados ${items.length} items com sucesso!`);
        let msg =  `Item alterado com sucesso!`; 
        this.toastr.success(msg, "CyberMessenger");
        this._items$.next(this.listaItems);
        done();
      };
    }, error => {
      let msg = `Ocorreu uma falha na atualizaÃ§Ã£o dos dados. Tente novemente!`;
      this.toastr.error(msg, "CyberMessenger");
      console.log('Erro ao salvar registro: ' + error.error.message);
    });
  }

  /**
	 * Handle Http operation that succeded.
	 * @param operation - name of the operation that succeded
	 * @param result - Result value to return as the observable
	 */
  private log<Politica>(operation: string, result: Politica) {
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} succeded`);

    // Let the app keep running by returning an empty result.
    //return of(result as Politica);
    return result;
  }

	/**
	 * Handle Http operation that failed.
	 * Let the app continue.
	 * @param operation - name of the operation that failed
	 * @param result - optional value to return as the observable result
	 */
  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }
 
    // TODO: better job of transforming error for user consumption
    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as Politica);
    return throwError(
      'Something bad happened; please try again later.');
  }
}
