import { Injectable, Inject, InjectionToken } from '@angular/core';
import {
  HttpRequest,
  HttpHandler,
  HttpEvent,
  HttpInterceptor,
  HttpResponse,
  HttpErrorResponse
} from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { environment } from '../../environments/environment';
import { ActivatedRoute } from '@angular/router';
import { IAppConfig } from '../models/app-config.model';
import { timeout, tap } from 'rxjs/operators';

export const DEFAULT_TIMEOUT = new InjectionToken<number>('defaultTimeout');

@Injectable({
  providedIn: 'root'
})
export class PackageUrlInterceptor implements HttpInterceptor {
  private envConfig: IAppConfig;

  constructor(private route: ActivatedRoute, @Inject(DEFAULT_TIMEOUT) protected defaultTimeout: number) {
    this.envConfig = environment;
  }
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
    const timeoutValue = request.headers.get('timeout') || this.envConfig.timeout || this.defaultTimeout;
    const timeoutValueNumeric = Number(timeoutValue);

    if (request.url.indexOf(environment.localeFileType) === -1 && request.url.indexOf(environment.configFileURL) === -1) {
      let clone = request.clone({
        url: this.envConfig.conppwPadraoEndpoint + request.url
      });
      
      return next.handle(clone).
        pipe(
          timeout(timeoutValueNumeric),
          tap((event: HttpEvent<any>) => {
            if (event instanceof HttpResponse) {
              this.log('HttpResponse', `${environment.projectName}: request realizado=${event.status}/${event.statusText}`);
            }
          }, error => {
            console.log(`Could not handle request: ${JSON.stringify(error)}`);
            this.logError('HttpResponse', error)
          })
      );
    }
    return next.handle(request).pipe(
      timeout(this.envConfig.timeout || this.defaultTimeout)
    );
  }

  private log<T>(operation: string, result: T) {
    console.log(`Carga de configuraÃ§Ãµes realizada: ${operation} `);

    return result;
  }

  private logError(operation: string, result: HttpErrorResponse) {
    // TODO: send the error to remote logging infrastructure
    if (result.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error('An error occurred:', result.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${result.status}, ` +
        `body was: ${result.error}`);
    }

    console.log(`${operation} failed: ${result.message}`);

    // Let the app keep running by returning an empty result.
    //return of(result as T);
    return throwError(
      'Something bad happened; please try again later.');
  }
}
