import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { PoliticaComponent } from './politica.component';
import { PoliticaListComponent } from './list/politica-list.component';
import { PoliticaEditComponent } from './editor/politica-edit.component';
import { ProjectConfigResolver } from './interceptors/project.config.resolver';

@NgModule({
  imports: [
      RouterModule.forChild([
        {path: '', component: PoliticaComponent,
          children: [
            {path: '', component: PoliticaListComponent},
            {path: ':openType',component: PoliticaEditComponent},
            {path: ':openType/:code',component: PoliticaEditComponent }
          ],resolve: {
            envConfiguration: ProjectConfigResolver
          }}
    ])
  ],
  exports: [RouterModule]
})
export class PoliticaRoutingModule { }
