import { CommonModule, DecimalPipe } from '@angular/common';
import { NgModule } from '@angular/core';

import { FormsModule } from '@angular/forms';
import { HttpClientModule, HttpClient }    from '@angular/common/http';
import { TranslateModule, TranslateLoader, MissingTranslationHandler } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { FontAwesomeModule } from '@fortawesome/angular-fontawesome';
import { NgbTypeaheadModule, NgbPaginationModule, NgbModalModule, NgbDatepickerModule, NgbDropdownModule } from '@ng-bootstrap/ng-bootstrap';
import { ToastrModule } from 'ngx-toastr';

import { PoliticaListComponent } from './list/politica-list.component';
import { PoliticaEditComponent } from './editor/politica-edit.component';

import { NgbdSortableHeader } from './directives/sortable.directive';
import { PoliticaService } from './services/politica.service';
import { PoliticaRoutingModule } from './politica-routing.module';
import { PPWMissingTranslationHandler } from './util/missingTranslationHandler';
import { PoliticaComponent } from './politica.component';
import { HttpErrorHandler } from './services/http-error-handler.service';
import { FilaService } from './services/fila.service';
import { PPWInterceptorProviders } from './interceptors/ppw.interceptor.provider';
import { DropDownLabelPipe } from './util/DropDownLabelPipe';

import { environment } from '../environments/environment';


//import { environment } from 'src/environments/environment';

//const localeStartingURL = environment.localeUrlFolder;
//const localeFileType = environment.localeFileType;

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, environment.localeUrlFolder, environment.localeFileType);
}


@NgModule({
  declarations: [PoliticaComponent,
                 NgbdSortableHeader, 
                 PoliticaListComponent, 
                 PoliticaEditComponent,
                 DropDownLabelPipe
  ],
  imports: [
  CommonModule,
  FontAwesomeModule,
  FormsModule,
  HttpClientModule,
  NgbModalModule,
  NgbDatepickerModule,
  NgbTypeaheadModule,
  NgbPaginationModule,
  NgbDropdownModule,
  ToastrModule.forRoot() ,
  TranslateModule.forChild({
    missingTranslationHandler: {provide: MissingTranslationHandler, useClass: PPWMissingTranslationHandler},
    loader: { provide: TranslateLoader, useFactory: HttpLoaderFactory, deps: [HttpClient]},
      isolate: true
  }),
  PoliticaRoutingModule
  ],
  providers:[PPWInterceptorProviders,   HttpErrorHandler,FilaService,PoliticaService,DecimalPipe],
  bootstrap: [PoliticaComponent]
})
export class PoliticaModule{}
