import { Component, OnInit, ViewChildren, QueryList, OnDestroy } from '@angular/core';
import { Politica } from './models/politica.model';
import { PoliticaService } from './services/politica.service';
import { NgbdSortableHeader } from './directives/sortable.directive';
import { faTimes } from '@fortawesome/free-solid-svg-icons';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable, Subscription } from 'rxjs';
import { release } from '../environments/release';

@Component({
  selector: 'lib-root',
  templateUrl: './politica.component.html',
  styleUrls: ['./politica.component.scss']
})
export class PoliticaComponent implements OnInit,OnDestroy {

  goBackLink: string;
  openType: string = "";
  resourceId: string = "";
  faTimes = faTimes;
  public items = [];
  condition = [] as boolean[];
  contador: number = 0;

  showLoading: boolean = false;
  arqLoading$: Observable<boolean>;

  loadSubs: Subscription;
  release: string = release.version;

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
    private router: Router,
    private politicaService: PoliticaService,
    private toastr: ToastrService,
    public translate: TranslateService) {
    this.arqLoading$ = this.politicaService.loading$;
    this.loadSubs = this.arqLoading$.subscribe((status) => {
      this.showLoading = status;
    });
    this.goBackLink = '/external-' + this.politicaService.getResourceName();

  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      console.log(params)
      if (params.hasOwnProperty("openType"))
        this.openType = params.get("openType");
      if (params.hasOwnProperty("code"))
        this.resourceId = params.get("code");
    });

    /*this.politicaService.load().subscribe((data) => {
      this.items = Array.from(Object.keys(data), k => data[k]);
      let itemsBureau = this.items;
      itemsBureau.forEach(i => {
        console.log(i);
      });
    });*/
  }

  ngOnDestroy() {
    console.log('Cleaning Politica...');
    if (this.loadSubs != undefined) this.loadSubs.unsubscribe();
  }
}
