import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { environment } from '../../environments/environment';
import { tap } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot } from '@angular/router';

export interface IAppConfig {
    cliente: string;
    package: string;
    conppwPadraoEndpoint: string;
    extratorEndpoint: string;
    consoleEndpoint: string;
    modules?:Array<string>;
    configFileURL: string;
    localeUrlFolder: string;
    localeFileType: string;
    dynamicEnv: boolean;
    timeout: number;
}

@Injectable({
    providedIn: 'root'
})
export class ProjectConfigResolver implements Resolve<IAppConfig> {
    private _serviceName: string = 'ProjectConfigService';

    static settings: IAppConfig = environment;
    static loaded: boolean = false;

    constructor(private http: HttpClient) {
    }

    resolve(
        route: ActivatedRouteSnapshot,
        state: RouterStateSnapshot
    ): Observable<IAppConfig> | Promise<IAppConfig> | IAppConfig {
        if (!ProjectConfigResolver.settings.dynamicEnv)
            ProjectConfigResolver.loaded = true;

        if (ProjectConfigResolver.loaded)
            return ProjectConfigResolver.settings;

        return this.http.get<IAppConfig>(environment.configFileURL).pipe(
            tap(data => {
                this.log(`${this._serviceName}/load`, data);
                ProjectConfigResolver.overrideStaticConfiguration(<IAppConfig>data);
                ProjectConfigResolver.loaded = true;
            }, error => {
                console.log(`Could not load file '${environment.configFileURL}': ${JSON.stringify(error)}`);
                this.logError(`${this._serviceName}/load`, error)
            }));
    }


    private static overrideStaticConfiguration(configObject: IAppConfig) {
        ProjectConfigResolver.settings = configObject;
        for (let key in configObject) {
            if (configObject.hasOwnProperty(key)) {
                environment[key] = configObject[key];
            }
        }
    }

    private log<T>(operation: string, result: T) {
        console.log(`Carga de configuraÃ§Ãµes realizada: ${operation} `);

        return result;
    }

    private logError(operation: string, result: HttpErrorResponse) {
        // TODO: send the error to remote logging infrastructure
        if (result.error instanceof ErrorEvent) {
            // A client-side or network error occurred. Handle it accordingly.
            console.error('An error occurred:', result.error.message);
        } else {
            // The backend returned an unsuccessful response code.
            // The response body may contain clues as to what went wrong,
            console.error(
                `Backend returned code ${result.status}, ` +
                `body was: ${result.error}`);
        }

        console.log(`${operation} failed: ${result.message}`);

        // Let the app keep running by returning an empty result.
        //return of(result as T);
        return throwError(
            'Something bad happened; please try again later.');
    }

}