import { Component, OnInit, ViewChildren, QueryList } from '@angular/core';
import { PoliticaService } from '../services/politica.service';
import { faPlus, faPencilAlt, faTrashAlt, faSearch, faSort } from '@fortawesome/free-solid-svg-icons';
import { Observable } from 'rxjs';
import { Politica } from '../models/politica.model';
import { NgbdSortableHeader, SortEvent } from '../directives/sortable.directive';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'politica-list',
  templateUrl: './politica-list.component.html',
  styleUrls: ['../politica.component.scss']
})
export class PoliticaListComponent implements OnInit {
  title = '';
  resourceName = '/';
  faPlus = faPlus;
  faPencilAlt = faPencilAlt;
  faTrashAlt = faTrashAlt;
  faSearch = faSearch;
  faSort = faSort;
  total$: Observable<number>;
  politicas$: Observable<Politica[]>;
  public items = [];
  condition = [] as boolean [];
  contador:number = 0;
  static LOCALE_KEY = 'currentLocale';


  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;
  constructor(private route: ActivatedRoute,
              private router: Router,
              private politicaService: PoliticaService,
              public translate:TranslateService) {

    this.politicas$ = politicaService.items$;
    this.total$ = politicaService.total$;
    this.resourceName = '/external-'+ this.politicaService.getResourceName();
    translate.setDefaultLang('ptBR');
    const currentLocale =  sessionStorage.getItem(PoliticaListComponent.LOCALE_KEY);
        
    translate.setDefaultLang(currentLocale);
  }

  get page() { return this.politicaService.page; }
  get pageSize() { return this.politicaService.pageSize; }
  get searchTerm() { return this.politicaService.searchTerm; }
  set page(page: number) { this.politicaService.page =  page;}
  set pageSize(pageSize: number) { this.politicaService.pageSize =  pageSize; }
  set searchTerm(searchTerm: string) {  this.politicaService.searchTerm = searchTerm; }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.politicaService.sortColumn = column;
    this.politicaService.sortDirection = direction;
  }
  
  ngOnInit() {
    this.politicaService.load();
  }
  
  create(): void {
    this.router.navigate([this.resourceName, 'create']);
  }

  change(indexId:string): void {
    this.router.navigate([this.resourceName, 'change', indexId]);
  }

  remove(indexId:string): void {
    this.router.navigate([this.resourceName, 'remove', indexId]);
  }

  clear(): void {
    this.searchTerm = "";
    this.headers.forEach(header => {
        header.direction = '';
    });
    this.politicaService.load();
  }

}
