import { Injectable, PipeTransform } from '@angular/core';
import { HttpHeaders, HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, of, Subject } from 'rxjs';
import { tap, catchError, debounceTime, switchMap,delay } from 'rxjs/operators';
import { Fila } from '../models/fila.model';
import { State } from '../models/state.model';
import { SortDirection } from '../directives/sortable.directive';
import { SearchResult } from '../models/search-result.model';

import { environment } from '../../environments/environment';
import { ToastrService } from 'ngx-toastr';
import { HttpErrorHandler } from './http-error-handler.service';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(items: Fila[], column: string, direction: string): Fila[] {
  if (direction === '') {
    return items;
  } else {
    return [...items].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(item: Fila, text: string) {
  const term = text.toLowerCase();
  return (item.nomeFila.toLowerCase().includes(term));
}

@Injectable({
  providedIn: 'root'
})
export class FilaService {

  //private serviceUrl = 'politica';
  private _serviceName = "FilaService";
  private listaFilas: Fila[] = [];
  private _filas$ = new BehaviorSubject<Fila[]>([]);
  private _loading$ = new BehaviorSubject<boolean>(false);
  private _search$ = new Subject<void>();
  private _total$ = new BehaviorSubject<number>(0);

  private _state: State = {
    page: 1,
    pageSize: 10,
    codigo: '',
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  constructor(private http: HttpClient,
              private httpErrorHandler: HttpErrorHandler,
              private toastr: ToastrService) {
    this.handleError = httpErrorHandler.createHandleError(this._serviceName);
    this.load();
    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(200),
      switchMap(() => this._search()),
      delay(200),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._filas$.next(result.items);
      this._total$.next(result.total);
    });
  }

  get filas$(){return this._filas$.asObservable();}
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult<Fila>> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

    // 1. sort
    let items = sort(this.listaFilas, sortColumn, sortDirection);

    // 2. filter
    items = items.filter(item => matches(item, searchTerm));
    const total = items.length;

    // 3. paginate
    items = items.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);

    //this.messageService.add("success", this._serviceName + ": Lista de items selecionados");

    return of({ items, total });

  }
  
  load(): void {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de filas selecionadas");
    this.getFilas().subscribe(
        data => {
          this.listaFilas = Array.from(Object.keys(data), k=>data[k]);
          this._filas$.next(this.listaFilas);
          this._search$.next();
          console.log(this.listaFilas);
        }
      );
  }

  getFilas(): Observable<Fila[]> {
    //this.messageService.add("info", this._serviceName + " Lista de items selecionados");
    console.log(this._serviceName + " Lista de filas selecionadas");
    return this.http.get<Fila[]>(`${environment.serviceUrls.filaUrl}`)
      .pipe(tap(_ => console.log(`fetched items`)),
        catchError(this.handleError<Fila[]>('getFilas', []))
      );
  }

  deleteItem(item: Fila, keyName?: string): Observable<Fila> {
    const id = typeof item === 'string' ? item : item[keyName];
    return this.http.delete<Fila>(`${environment.serviceUrls.filaUrl}/${id}`, httpOptions).pipe(
      tap(_ => {
        console.log(`item removido com cÃ³digo=${id}`)
      }),
      catchError(this.handleError<Fila>('deleteItem'))
    );
  }

  putItem(item: Fila, keyName: string): Observable<any> {
    //return this.http.put(`${environment.serviceUrls.filaUrl}/${item[keyName]}`, item, httpOptions).pipe(
    return this.http.put(`${environment.serviceUrls.filaUrl}`, item, httpOptions).pipe(
      tap(_ => {console.log(`${this._serviceName}: Item cÃ³digo=${item[keyName]} atualizada com sucesso!`);
      }),
      catchError(this.handleError<Fila>(`updateItem cÃ³digo=${item[keyName]}`))
    );
  }

  postItems(items: Fila[]): Observable<Fila[]> {
    return this.http.post<Fila[]>(environment.serviceUrls.filaUrl, items, httpOptions).pipe(
      tap((newItems: Fila[]) => console.log(`${this._serviceName}: Foram atualizadas ${newItems.length} items com sucesso!`)),
      catchError(this.handleError<Fila[]>(`postItems cÃ³digo=${items.length}`))
    );
  }

  private handleError<Fila>(operation = 'operation', result?: Fila) {
    return (error: any): Observable<Fila> => {

      // TODO: send the error to remote logging infrastructure
      console.error(error); // log to console instead

      // TODO: better job of transforming error for user consumption
      console.log(`${operation} failed: ${error.message}`);

      // Let the app keep running by returning an empty result.
      return of(result as Fila);
    };
  }
}
